import styled from 'styled-components'

export default styled.header`
   grid-area: header;
   background-color: #ea5b0c;

   display: flex;
   justify-content: center;
   align-items: center;

   

   h1 {
       font-size: 1.4rem;
       font-weight: 300;
       color: #ddd;
       margin: 0;
   }
   img {
    width: 20vh;
    height: 20vh;
    
}
      
`