import React from 'react'
import './Dashboard.css'

const Dashboard = props => {
    return (
        <div className="body-1">

            <div className="section-1">
                <div id="title">
                    <div className="txt">
                    <h1>
                        Reviva Coleta de Óleo
                    </h1>
                    <span>
                        Não jogue óleo de cozinha na pia o meio ambiente agradece! Recicle.
                    </span>
                  
                    </div>
                    <div className="img">
                        <img src="./img/logoOrange.png" alt="" />

                    </div>
                    <span>
                        ESENCIAL É RENOVAR
                    </span>

                </div>




                <h2>PASSO A PASSO PARA O DESCARTE</h2>

                <div id="cards">
                    <div className="card">
                        <img src="./img/vetor/fase-1.png" alt="" />
                        <strong> 1º Passo</strong>
                        <p>Espere o óleo esfriar na panela.</p>
                    </div>
                    <div className="card">
                        <img src="./img/vetor/fase-2.png" alt="" />
                        <strong> 2º Passo</strong>
                        <p>Despeje o óleo na garrafa usando um funil.</p>
                    </div>
                    <div className="card">
                        <img src="./img/vetor/fase-3.png" alt="" />
                        <strong> 3º Passo</strong>
                        <p>Feche a garrafa. Isso evita odores e insetos.</p>
                    </div>
                    <div className="card">
                        <img src="./img/vetor/fase-4.png" alt="" />
                        <strong> 4º Passo</strong>
                        <p>Limpe a panela e o funil com um guardanapo e descarte-o no lixo orgânico.</p>
                    </div>
                    <div className="card">
                        <img src="./img/vetor/fase-5.png" alt="" />
                        <strong> 5º Passo</strong>
                        <p>Guarde até que nossa equipe venha retirar</p>
                    </div>

                </div>
            </div>
        </div>
    )
}


export default Dashboard